:root{
    --bgcolor : #FBFBFA;
    --iblue :#0095F7;
    --bodybg:#FEFFFE;
    --blue-dark: #002ead;

}
* {
  margin: 0;
  padding: 0;
}
body{
    background: var(--bodybg);
}

.notification{
    width: fit-content;
    background-color: rgb(202 255 197);
    margin: 10px auto 10px auto;
    color: rgb(34 163 21);
    font-weight: 700;
    padding: 10px;
    border-radius: 10px;
  }
  .notification  span{
    margin-top: 0;
  }
  
  .notification a{
    color: #0095F7;
  }
 


.playground{
    background-color: var(--bgcolor);
    padding: 10px;
    border: #121212 3px solid;
}

.playground h2{
    font-family: 'Pangolin';
    letter-spacing: 2px;
}
.container {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  margin: 20px;
}



.canvas {
  width: fit-content;
  border: 3px black solid;
}

.canvas-container {
  position: relative;
}

.svg {
  position: absolute;
  display: block;
  width: 300px;
  height: 350px;
  top: 23%;
  left: 5%;
  /* transform: translate(-40%, -34%); */
  margin: auto;
}
.spody-canvas{
    position: absolute;
    display: block;
    width: 450px;
    height: 450px;
    top: 50%;
    left: 50%;
    transform: translate(-40%, -34%);
    margin: auto;
}


.spody-jersey{
    position: absolute;
    top: -25%;
    left: -24%;
    
}

.spody-jersey img{
    /* width: 300px; */
    height: 550px;
    
}

.save {
  width: fit-content;
  margin: auto;
}
span{
    display: block;
    text-align: left;
    font-size: small;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin: 8px 0 0 0;
}

.brush-view{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border: #121212 solid 3px;
    padding: 10px;
    height: 100px;
    width: 100px;
    margin: 20px auto 20px auto;
    

}

.brush-preview{
    border-radius: 100%;
    border: #121212 solid 1px;
    
}

.colorpicker input:hover{
    cursor:pointer;

}

.spody .spody-title{
    margin: 20px 0 0 0;
    font-size: medium;
    text-align: center;
    font-family: 'Pangolin';
    letter-spacing: 2px;
    /* text-decoration:underline; */
}

.hide-show{
    /* display: block; */
    margin: 8px auto 4px auto;
    background-color: var(--bgcolor);
    color: var(--iblue);
    border: none;
    cursor: pointer;
    margin: 0 4px 4px 4px;
}
.hide-show:hover{
    color: #006fb9;

}

.save-button button{    
    margin: 20px 0;
    background-color:var(--iblue);
    color: #fefefe;
    stroke: white 2px;
    font-size: large;
    font-family: monospace;
    font-weight: 500;
    width: fit-content;
    padding: 4px 8px 4px 8px;
    border: none;
    /* border: 3px #121212 solid; */
    cursor: pointer;
}
.save-button img{
    width: 20px;
    
}
.save-button button:hover{
    background-color: var(--blue-dark);

}

input[type="text"] {
    padding: 4px;
    /* font-size: medium; */
	/* -webkit-appearance: none; */
	/* border: none; */
	width: 100%;
	height: 24px;
    border: #121212 solid;
    box-sizing: border-box;
}
    

input[type="color"] {
	-webkit-appearance: none;
	border: none;
	width: 100%;
	height: 20px;
    border: #121212 solid;
    box-sizing: border-box;

    
}
input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}
input[type="color"]::-webkit-color-swatch {
	border: none;
}



input[type="range"] { 
    margin: auto;
    -webkit-appearance: none;
    position: relative;
    overflow: hidden;
    height: 20px;
    width: 100%;
    cursor: pointer;
    border: #121212 solid ;
    box-sizing: border-box;
    border-radius: 0; /* iOS */
}

::-webkit-slider-runnable-track {
    background: #ddd;
}

/*
 * 1. Set to 0 width and remove border for a slider without a thumb
 */
::-webkit-slider-thumb {
    -webkit-appearance: none;
    width: 20px; /* 1 */
    height: 20px;
    background: #fff;
    box-sizing: border-box;
    box-shadow: -100vw 0 0 100vw rgb(29, 29, 29);
    border: 4px solid #121212; 
}

::-moz-range-track {
    height: 40px;
    background: #ddd;
}

::-moz-range-thumb {
    background: #fff;
    height: 40px;
    width: 20px;
    border: 3px solid #999;
    border-radius: 0 !important;
    box-shadow: -100vw 0 0 100vw #121212;
    box-sizing: border-box;
}

::-ms-fill-lower { 
    background: #121212;
}

::-ms-thumb { 
    background: #fff;
    border: 2px solid #999;
    height: 40px;
    width: 20px;
    box-sizing: border-box;
}

::-ms-ticks-after { 
    display: none; 
}

::-ms-ticks-before { 
    display: none; 
}

::-ms-track { 
    background: #ddd;
    color: transparent;
    height: 40px;
    border: none;
}

::-ms-tooltip { 
    display: none;
}

.canvas-control{
    border: #121212 3px solid;
    border-bottom: none;
    width: fit-content;
    margin: auto;
    box-sizing: border-box;
    background-color: var(--bgcolor);
}