.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav{
  margin-bottom: 10px;
  border-bottom: #121212 3px solid;
  display: flex;
  padding: 10px;
  align-items:center;
  background-color: #e200b1;

  
}

.nav div{
  display: flex;
  align-items:center;
  background-color: #e200b1;
  cursor: pointer;
}
.nav h1{
  color: #121212;
  margin: 8px 0 0 8px;
  font-family: 'Pangolin';
  letter-spacing: 2px;
  font-size: 40px;
}
.nav span{
  margin: 0 0 0 10px;
  background-color: #121212;
  color: white;
  padding: 0 10px 0 10px;
  align-self:flex-end;
}

.nav .beta{
  font-family: monospace;
}
.nav img{
  height: 60px;
  /* background-color: rgb(255 55 212); */
  background-color: rgb(243 246 96);
  border: #121212 3px solid;

}

.github{
  display: block;
  text-align: end;
}
.github a{
  text-decoration: none;
  font-size: small;
  color: rgb(136, 136, 136);
  
}

