*{
    margin: 0%;
    padding: 0%;
    
}
:root{
    --bgcolor : #FBFBFA;
    --iblue :#0095F7;
    --bodybg:#FEFFFE;
}

.jersey-container{
    display: grid;
    grid-template-columns: auto auto;
    background-color: var(--bgcolor);
    padding: 10px;
    border: 3px #121212 solid;
    width: fit-content;
    overflow: auto;
    height: 500px;
    
}
.jersey{
    display: grid;
    grid-template-columns: auto auto auto;
    column-gap: 10px;
    row-gap: 10px;
}

.Tshirts{
    padding: 10px;
    background-color: #FEFFFE;
    border: 3px solid #121212;
    cursor: pointer;
    height: 90px;
}

.Tshirts:hover{
    padding: 10px;
    background-color: rgb(233, 233, 233);
}
.jersey img{
    width:60px;
}

.jersey span{
    margin: 0%;
    font-size: small;
    text-align: center;
}